/*Main color*/
@import url(http://fonts.googleapis.com/css?family=Raleway:400,100,200,300);
/*Datepickk*/
#Datepickk {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 999;
  width: 100%;
  height: 100%;
  font-family: 'Raleway';
  user-select: none;
}
#Datepickk.MSIE:not(.wrapped):not(.inline) .d-calendar {
  height: 560px;
}
#Datepickk.show .d-overlay {
  animation-name: show;
  animation-duration: .15s;
  animation-timing-function: ease-out;
  animation-fill-mode: both;
}
#Datepickk.show .d-calendar {
  animation-name: animJelly;
  animation-duration: 1s;
  animation-delay: .1s;
  animation-timing-function: linear;
  animation-fill-mode: both;
}
#Datepickk.hide {
  animation-name: hide;
  animation-duration: .1s;
  animation-timing-function: linear;
  animation-fill-mode: both;
}
#Datepickk.hide .d-overlay {
  animation-name: show;
  animation-duration: .15s;
  animation-timing-function: ease-out;
  animation-fill-mode: both;
  animation-direction: reverse;
}
#Datepickk .d-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(55, 58, 71, 0.95);
  z-index: 1;
}
#Datepickk .d-title {
  color: white;
  text-align: center;
  position: relative;
  font-size: 30px;
  margin: 25px 0;
  z-index: 2;
}
#Datepickk .d-confirm {
  border: none;
  position: relative;
  background-color: transparent;
  outline: none;
  font-family: inherit;
  font-size: 30px;
  font-weight: 500;
  color: white;
  margin: 25px 0;
  cursor: pointer;
  padding: 0;
  z-index: 2;
}
#Datepickk .d-title:empty,
#Datepickk .d-confirm:empty {
  display: none;
}
@media (max-height: 528px) {
  #Datepickk .d-title,
  #Datepickk .d-confirm {
    font-size: 20px;
    margin: 15px 0;
  }
  #Datepickk .d-calendar {
    font-size: 20px;
  }
}
#Datepickk.multi .d-calendar,
#Datepickk.multi .d-legend {
  max-width: 800px;
}
#Datepickk.inline.show .d-calendar {
  animation: none;
}
#Datepickk.inline,
#Datepickk.wrapped {
  position: static;
  z-index: 0;
}
#Datepickk.inline .d-calendar,
#Datepickk.wrapped .d-calendar {
  box-shadow: none;
  z-index: 0;
  max-width: none;
  max-height: none;
}
#Datepickk.inline .d-confirm,
#Datepickk.wrapped .d-confirm,
#Datepickk.inline .d-title,
#Datepickk.wrapped .d-title {
  color: #222;
}
#Datepickk.inline .d-overlay,
#Datepickk.wrapped .d-overlay {
  display: none;
}
#Datepickk.inline .d-legend,
#Datepickk.wrapped .d-legend {
  color: #222!important;
  max-width: none;
  max-height: none;
}
#Datepickk.fullscreen .d-calendar {
  max-width: none;
  max-height: none;
}
#Datepickk.fullscreen .d-calendar input + label {
  outline: 1px solid #eaeaea;
  box-sizing: border-box;
  align-items: flex-start !important;
  justify-content: flex-end !important;
}
#Datepickk.fullscreen .d-calendar input + label text {
  padding: 5px 10px 0 0;
}
#Datepickk.fullscreen .d-legend {
  max-width: none;
}
#Datepickk .d-legend {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  z-index: 2;
  max-width: 500px;
  position: relative;
  color: white;
}
#Datepickk .d-legend:empty {
  display: none;
}
#Datepickk .d-legend p {
  backface-visibility: visible !important;
  animation-name: fadeInUp;
  animation-duration: .5s;
  animation-fill-mode: both;
  margin: 0;
  padding: 15px 30px 15px 0;
  cursor: pointer;
}
#Datepickk .d-legend p:after {
  content: attr(data-legend);
}
#Datepickk .d-legend p span {
  width: 20px;
  height: 20px;
  border-radius: 100%;
  vertical-align: bottom;
  display: inline-block;
  margin-right: 10px;
}
#Datepickk .d-calendar {
  margin: 0;
  background-color: white;
  box-shadow: 0px 2px 10px -2px rgba(0, 0, 0, 0.6);
  font-size: 20px;
  width: 100%;
  position: relative;
  max-width: 500px;
  max-height: 560px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-grow: 1;
  z-index: 2;
}
#Datepickk .d-calendar .d-header {
  position: relative;
  background-color: #1B363F;
  color: white;
  font-size: 1.5em;
}
#Datepickk .d-calendar .d-header p {
  margin: .5em 0;
  text-align: center;
}
#Datepickk .d-calendar .d-header p .d-month {
  cursor: pointer;
  white-space: nowrap;
}
#Datepickk .d-calendar .d-header p .d-year {
  margin-left: 10px;
  cursor: pointer;
}
#Datepickk .d-calendar .d-header p .d-year:before {
  content: "";
}
#Datepickk .d-calendar .d-header i {
  position: absolute;
  top: 50%;
  width: 30px;
  height: 30px;
  cursor: pointer;
  text-align: center;
  border-radius: 100%;
  transition: background-color 0.2s ease;
  transform: translateY(-50%);
}
#Datepickk .d-calendar .d-header i:before,
#Datepickk .d-calendar .d-header i:after {
  content: '';
  width: 0;
  height: 0;
  position: absolute;
}
#Datepickk .d-calendar .d-header i:before {
  border-top: 10px solid rgba(0, 0, 0, 0);
  border-bottom: 10px solid rgba(0, 0, 0, 0);
}
#Datepickk .d-calendar .d-header i:after {
  border-top: 7px solid rgba(0, 0, 0, 0);
  border-bottom: 7px solid rgba(0, 0, 0, 0);
  transition: border-color 0.2s ease;
}
#Datepickk .d-calendar .d-header i#d-previous {
  left: 20px;
}
#Datepickk .d-calendar .d-header i#d-previous:before {
  border-right: 10px solid #FFF;
  top: 5px;
  left: 7px;
}
#Datepickk .d-calendar .d-header i#d-previous:after {
  border-right: 7px solid #1B363F;
  top: 8px;
  left: 10px;
}
#Datepickk .d-calendar .d-header i#d-next {
  right: 20px;
}
#Datepickk .d-calendar .d-header i#d-next:before {
  border-left: 10px solid #FFF;
  top: 5px;
  left: 12px;
}
#Datepickk .d-calendar .d-header i#d-next:after {
  border-left: 7px solid #1B363F;
  top: 8px;
  left: 12px;
}
#Datepickk .d-calendar .d-header i:hover,
#Datepickk .d-calendar .d-header i:focus {
  background-color: #5E7178;
}
#Datepickk .d-calendar .d-header i:hover:after,
#Datepickk .d-calendar .d-header i:focus:after {
  border-left-color: #5E7178!important;
  border-right-color: #5E7178!important;
}
#Datepickk .d-calendar .d-month-picker {
  display: flex;
  justify-content: space-between;
  background-color: rgba(27, 54, 63, 0.9);
  height: 0;
  opacity: 0;
  pointer-events: none;
  transition: height 0.2s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}
#Datepickk .d-calendar .d-month-picker.show {
  height: 44px;
  opacity: 1;
  pointer-events: auto;
}
#Datepickk .d-calendar .d-month-picker > div {
  width: calc(100% / 12);
  text-align: center;
  line-height: 44px;
  color: white;
  cursor: pointer;
}
#Datepickk .d-calendar .d-month-picker > div:hover,
#Datepickk .d-calendar .d-month-picker > div:focus {
  background-color: #b67a00;
  color: white;
}
#Datepickk .d-calendar .d-month-picker > div.current {
  background-color: #d5e900;
  color: black;
}
#Datepickk .d-calendar .d-year-picker {
  display: flex;
  justify-content: space-between;
  background-color: rgba(27, 54, 63, 0.9);
  height: 0;
  opacity: 0;
  pointer-events: none;
  transition: height 0.2s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}
#Datepickk .d-calendar .d-year-picker.show {
  height: 44px;
  opacity: 1;
  pointer-events: auto;
}
#Datepickk .d-calendar .d-year-picker > div {
  width: calc(100% / 11);
  text-align: center;
  line-height: 44px;
  color: white;
  cursor: pointer;
}
#Datepickk .d-calendar .d-year-picker > div:hover,
#Datepickk .d-calendar .d-year-picker > div:focus {
  background-color: #b67a00;
  color: white;
}
#Datepickk .d-calendar .d-year-picker > div.current {
  background-color: #d5e900;
  color: black;
}
#Datepickk .d-calendar .d-weekdays {
  display: flex;
}
#Datepickk .d-calendar .d-week {
  background-color: #e99c00;
  color: white;
  display: flex;
  width: 100%;
}
#Datepickk .d-calendar .d-week + .d-week {
  border-left: 1px solid rgba(255, 255, 255, 0.05);
}
#Datepickk .d-calendar .d-week > div {
  flex-basis: calc(100% / 7);
  text-align: center;
}
#Datepickk .d-calendar .d-week > div p {
  margin: .8em 0;
}
@media (max-height: 540px) {
  #Datepickk .d-calendar .d-week {
    display: none;
  }
}
#Datepickk .d-calendar .d-table {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  flex: 1;
}
#Datepickk .d-calendar .d-table + .d-table {
  border-left: 1px solid rgba(0, 0, 0, 0.05);
}
#Datepickk .d-calendar .d-table:first-child:nth-last-child(n + 3),
#Datepickk .d-calendar .d-table:first-child:nth-last-child(n + 3) ~ div {
  flex: 0;
  flex-basis: calc(100% / 3 - 1px);
  /*-1px due to browser render*/
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}
#Datepickk .d-calendar .d-table input {
  display: none;
}
#Datepickk .d-calendar .d-table input + label {
  flex-basis: calc(100% / 7);
  -ms-flex-preferred-size: 14.28%;
  /*Keep -> IE render fail*/
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.2s ease, background 0.2s ease, color 0.2s ease;
  position: relative;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  box-sizing: border-box;
}
#Datepickk .d-calendar .d-table input + label text {
  z-index: 9;
  pointer-events: none;
  position: relative;
}
#Datepickk .d-calendar .d-table input + label[style*="linear-gradient"] text {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 3px;
  padding: 0px 3px;
}
#Datepickk .d-calendar .d-table input + label.today text:before {
  content: '';
  width: 100%;
  height: 2px;
  background-color: #e99c00;
  position: absolute;
  bottom: -5px;
  left: 0;
}
#Datepickk .d-calendar .d-table input + label:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(233, 98, 0, 0.5);
  transition: transform 0.1s ease-out;
  transform: scaleX(0);
  transform-origin: left;
}
#Datepickk .d-calendar .d-table input + label.legend-hover {
  animation: legendhover 1s infinite;
  z-index: 10;
}
@keyframes legendhover {
  0% {
    transform: translate(0, 0px);
  }
  50% {
    transform: translate(0, -5px);
  }
  100% {
    transform: translate(0, 0px);
  }
}
#Datepickk .d-calendar .d-table input + label.next,
#Datepickk .d-calendar .d-table input + label.prev {
  color: #ccc;
}
#Datepickk .d-calendar .d-table input + label [data-tooltip]:after {
  content: '';
  border-radius: 100%;
  background-color: #e99c00;
  width: 5px;
  height: 5px;
  display: inline-block;
  vertical-align: text-top;
  transform: translateX(3px);
}
#Datepickk .d-calendar .d-table input + label .d-tooltip {
  position: absolute;
  background-color: #1B363F;
  color: #FFF;
  padding: 7px;
  font-size: .7em;
  z-index: 13;
  top: 100%;
  left: 50%;
  border-radius: 5px;
  transform: translate(-50%, -5px);
  display: none;
}
#Datepickk .d-calendar .d-table input + label .d-tooltip:before {
  content: '';
  border-bottom: 7px solid #1B363F;
  border-left: 5px solid rgba(0, 0, 0, 0);
  border-right: 5px solid rgba(0, 0, 0, 0);
  top: -7px;
  left: 50%;
  position: absolute;
  margin-left: -5px;
}
#Datepickk .d-calendar .d-table input + label .d-tooltip:empty {
  display: none!important;
}
#Datepickk .d-calendar .d-table input + label:hover .d-tooltip {
  display: block;
}
#Datepickk .d-calendar .d-table input:checked + label {
  color: white;
}
#Datepickk .d-calendar .d-table input:checked + label:before {
  transform: scaleX(1);
}
#Datepickk .d-calendar .d-table input:disabled + label {
  cursor: not-allowed;
}
#Datepickk .d-calendar .d-table input:disabled + label:after {
  content: '';
  position: absolute;
  top: 50%;
  left: 20%;
  width: 60%;
  height: 2px;
  z-index: 10;
  background-color: #c60000;
  transform-origin: center;
  transform: rotate(-25deg);
}
#Datepickk .d-calendar .d-table input + label.hidden {
  cursor: default;
  opacity: 0;
}
#Datepickk .d-calendar .d-table input + label.hidden:after {
  content: none;
}
#Datepickk .d-calendar .d-tables {
  display: flex;
  flex: 1;
  flex-wrap: wrap;
}
#Datepickk .d-calendar .d-tables:not(.locked) input:not(:checked) + label:not(.hidden):hover {
  color: #222;
  background-color: #eaeaea;
}
#Datepickk .d-calendar .d-tables.locked label {
  cursor: default;
}
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked + label {
  color: white;
}
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked + label ~ label:not(.hidden) {
  color: white;
}
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked + label ~ label:not(.hidden):before {
  transform: scaleX(1);
  background-color: rgba(233, 98, 0, 0.3);
}
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ label:hover ~ label,
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ label:focus ~ label {
  color: #666;
}
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ label:hover ~ label:before,
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ label:focus ~ label:before {
  transform: scaleX(0);
}
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ label:hover ~ label.next,
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ label:focus ~ label.next,
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ label:hover ~ label.prev,
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ label:focus ~ label.prev {
  color: #cccccc;
}
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ input:checked + label {
  color: white;
}
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ input:checked + label:before {
  transform: scaleX(1);
  background-color: rgba(233, 98, 0, 0.5);
}
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ input:checked + label ~ label {
  color: inherit;
}
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ input:checked + label ~ label.next,
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ input:checked + label ~ label.prev {
  color: #cccccc;
}
#Datepickk .d-calendar .d-tables.range:not(.before) input:not(.single):checked ~ input:checked + label ~ label:before {
  transform: scaleX(0);
}
#Datepickk .d-calendar .d-tables.range.before input:not(.single):not(:checked) + label {
  color: white;
}
#Datepickk .d-calendar .d-tables.range.before input:not(.single):not(:checked) + label:before {
  transform: scaleX(1);
  background-color: rgba(233, 98, 0, 0.3);
}
#Datepickk .d-calendar .d-tables.range.before input:not(.single):checked + label ~ label {
  color: inherit;
}
#Datepickk .d-calendar .d-tables.range.before input:not(.single):checked + label ~ label.next,
#Datepickk .d-calendar .d-tables.range.before input:not(.single):checked + label ~ label.prev {
  color: #cccccc;
}
#Datepickk .d-calendar .d-tables.range.before input:not(.single):checked + label ~ label:before {
  transform: scaleX(0);
}
#Datepickk .fadeInUp {
  backface-visibility: visible !important;
  animation-name: fadeInUp;
  animation-duration: .5s;
  animation-fill-mode: both;
}
@keyframes hide {
  100% {
    opacity: 0;
  }
}
@keyframes show {
  0% {
    transform: scale(1, 0.5);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}
.fadeInUp {
  animation-name: fadeInUp;
}
